//
// Bars
// --------------------------------------------------

.bar {
  position: fixed;
  right: 0;
  left: 0;
  z-index: $zindex-bar;
  height: $bar-base-height;
  padding-right: $bar-side-spacing;
  padding-left: $bar-side-spacing;
  border-bottom: $border-default;
  -webkit-backface-visibility: hidden; // Make sure the bar is visible when a modal animates in.
          backface-visibility: hidden;
}

// Modifier class to dock any bar below .bar-nav
.bar-header-secondary {
  top: $bar-base-height;
}

// Modifier class for footer bars
.bar-footer {
  bottom: 0;
}

// Modifier class to dock any bar above a standard bar
.bar-footer-secondary {
  bottom: $bar-base-height;
}

// Modifier class to dock any bar above a .bar-tab
.bar-footer-secondary-tab {
  bottom: $bar-tab-height;
}

// Give the footers the correct border
.bar-footer,
.bar-footer-secondary,
.bar-footer-secondary-tab {
  border-top: $border-default;;
  border-bottom: 0;
}


// Nav bar
// --------------------------------------------------

// Bar docked to top of viewport for showing page title and actions
.bar-nav {
  top: 0;
}

// Centered text in the .bar-nav
//
// We position the absolutely to make sure the title is always centered
.title {
  position: absolute;
  display: block;
  width: 100%;
  padding: 0;
  margin: 0 (-$bar-side-spacing);
  font-size: 1.0625rem;
  font-weight: $font-weight-medium;
  line-height: $bar-base-height;
  text-align: center;
  white-space: nowrap;
}
// Retain specified title color
.title a {
  color: inherit;
}


// Tab bar
// --------------------------------------------------

// Bar docked to bottom and used for primary app navigation
.bar-tab {
  display: table;
  bottom: 0;
  width: 100%;
  height: $bar-tab-height;
  padding: 0;
  table-layout: fixed;
  border-top: $border-default;
  border-bottom: 0;

  // Navigational tab (Nested to be more specific for the icons in tab-items)
  .tab-item {
    position: relative;
    display: table-cell;
    width: 1%;
    height: $bar-tab-height;
    color: #929292;
    text-align: center;
    vertical-align: middle;

    // Active states for the tab bar
    &.active,
    &:active {
      color: $gray-dark;
    }

    // Activity badge on an icon
    .badge {
      vertical-align: top;
      position: absolute;
      top: .1875rem;
      left: 50%;
    }

    // Tab icon
    .icon {
      top: .1875rem;
      width: 1.5rem;
      height: 1.5rem;
      padding-top: 0;
      padding-bottom: 0;

      // Make the text smaller if it's used with an icon
      ~ .tab-label {
        display: block;
        font-size: .6875rem;
      }
    }
  }
}

// Bars with buttons
// --------------------------------------------------

.bar .btn {
  position: relative;
  top: $btn-padding-y;
  z-index: 20; // Position the buttons on top of .title
  padding: .375rem .75rem .4375rem;
  margin-top: 0;
  font-size: .75rem;
  font-weight: $font-weight-light;
  line-height: 1;

  // Give buttons that are floated left and right side margin
  &.pull-right {
    margin-left: $bar-side-spacing;
  }
  &.pull-left {
    margin-right: $bar-side-spacing;
  }
}

// Bars with link buttons (Line the text up with content)
.bar .btn-link {
  top: 0;
  padding: 0;
  font-size: 1rem;
  line-height: $bar-base-height;
  color: $gray-dark;
  border: 0;

  &:active,
  &.active {
    color: darken($gray-dark, 10%);
  }
}

// Bars with block buttons
//
// Add proper padding
.bar .btn-block {
  top: .375rem;
  padding: .4375rem 0;
  margin-bottom: 0;
  font-size: 1rem; // Scale down font size to fit in bar.
  line-height: 1;
}

// Nav buttons (Only applicable within bars)
//
// Buttons inside bars that sit closer against the viewport.
.bar .btn-nav {
  &.pull-left {
    margin-left: -.3125rem;

    .icon-left-nav {
      margin-right: -.1875rem;
    }
  }
  &.pull-right {
    margin-right: -.3125rem;

    .icon-right-nav {
      margin-left: -.1875rem;
    }
  }
}


// Bars with Ratchicons
// --------------------------------------------------

.bar {
  .icon {
    position: relative;
    z-index: 20; // Position the buttons on top of .title
    padding-top: .625rem;
    padding-bottom: .625rem;
    font-size: 1.5rem;
    color: $gray-dark;
  }

  // Vertical center the larger icons in btns.
  .btn .icon {
    top: .1875rem;
    padding: 0;
  }

  // Handle carets in the titles
  .title .icon {
    padding: 0;

    // Specific postioning of the caret icon within a title. Used with popover.js.
    &.icon-caret {
      top: .25rem;
      margin-left: -.3125rem;
    }
  }
}


// Bars for search forms
// --------------------------------------------------

// Position/size search bar within the bar
.bar input[type="search"] {
  height: 1.8125rem;
  margin: .375rem 0;
}


// Bars with segmented controls
// --------------------------------------------------

// Position the control correctly inside a bar.
.bar .segmented-control {
  top: .5rem;
  margin: 0 auto;
}

// Position the nav correctly inside a bar.
.bar .nav {
    position: relative;
    margin: 0 auto;
    display: table
}

// Bars with input-group
// --------------------------------------------------

.bar {
  .input-group {
    position: relative;
    top: 0.3rem;
  }
}




// Color schemes
// --------------------------------------------------

// Dark links against a light background
.bar-light {
  .title {
    color: $bar-light-active-color;

    @include hover-focus {
      color: $bar-light-active-color;
    }
  }

  .bar-divider {
    background-color: rgba(0,0,0,.075);
  }
  border-color: rgba(0,0,0,.075);
}

// White links against a dark background
.bar-dark {
  .title {
    color: $bar-dark-active-color;

    @include hover-focus {
      color: $bar-dark-hover-color;
    }
  }
  .btn-link {
    color: $bar-dark-color;

    @include hover-focus {
      color: $bar-dark-hover-color;
    }
  }
  .icon {
    color: $bar-dark-color;

    @include hover-focus {
      color: $bar-dark-hover-color;
    }
  }
  .tab-item {
    color: $bar-dark-color;

    @include hover-focus {
      color: $bar-dark-hover-color;
    }
  }
  .tab-item.active,
  .tab-item.active .icon {
    @include plain-hover-focus {
      color: $bar-dark-active-color;
    }
  }
  .bar-divider {
    background-color: rgba(255,255,255,.075);
  }
  border-color: rgba(255,255,255,.075);
}